<template>
  <div>
    <div class="input_group">
      <div class="input_group_header">聚能搜</div>
      <form
        id="searchForm"
        action="https://kaifa.baidu.com/searchPage"
        method="get"
        target="_blank"
      >
        <input
          id="search"
          v-model="keyword"
          type="text"
          name="wd"
          placeholder="开发者搜索"
          baiduSug="2"
          clearable
          autocomplete="off"
        />
      </form>
      <div class="botton_group">
        <button type="submit" value="百度" class="btn baidu" @click="baidu">
          百度
        </button>
        <button type="submit" value="搜狗" class="btn sougou" @click="sougou">
          搜狗
        </button>
        <button type="submit" value="微信" class="btn weixin" @click="weixin">
          微信
        </button>
        <button type="submit" value="知乎" class="btn zhihu" @click="zhihu">
          知乎
        </button>
        <button type="submit" value="微博" class="btn weibo" @click="weibo">
          微博
        </button>
        <button type="submit" value="豆瓣" class="btn douban" @click="douban">
          豆瓣
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      keyword: ""
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  methods: {
    //封装方法
    toWeb(website, searchPath) {
      if (this.keyword) {
        window.open(
          website + '/' + searchPath + this.keyword,
          "_blank"
        );
        this.keyword = "";
      } else {
        window.open(website, "_blank");
      }
    },
    
    //百度搜索
    baidu() {
      let baidu = "https://www.baidu.com";
      let searchPath = "s?ie=utf-8&f=8&rsv_bp=1&tn=baidu&wd=";
      this.toWeb(baidu,searchPath)
    },
    //搜狗搜索
    sougou() {
      let sougou = "https://www.sogou.com";
      let searchPath = "=web?query"
      this.toWeb(sougou, searchPath)
    },
    //微信搜索
    weixin() {
      let weixin = "http://weixin.sogou.com";
      let searchPath = "weixin?type=2&query=";
    this.toWeb(weixin, searchPath);
    },
    //知乎搜索
    zhihu() {
      let zhihu = "https://www.zhihu.com";
      let searchPath = "/search?type=content&q="
      this.toWeb(zhihu,searchPath);
    },
    //微博搜索
    weibo() {
      if (this.keyword) {
        window.open("http://s.weibo.com/weibo/" + this.keyword, "_blank");
        this.keyword = "";
        return false;
      } else {
        window.open("https://weibo.com/", "_blank");
        return false;
      }
    },
    //豆瓣搜索
    douban() {
      let douban = "https://www.douban.com";
      let searchPath = "search?source=suggest&q=";
      this.toWeb(douban, searchPath);
    }
  }
};
</script>

